.notification-area {
  position: absolute;
  top: 60px;
  right: -300px;
  display: flex;
  flex-direction: column-reverse;
  height: 92vh;
  background-color: red;
  .notification-msg {
    width: 300px;
    height: 100px;
    padding: 20px;
    border-radius: 5px;
    translate: 200px 0;
    background-color: rgb(35, 49, 61);
    color: rgb(255, 255, 255);
    box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.171);
    transition: 0.8s;
    margin-bottom: 10px;
    opacity: 0.95;
    p {
      opacity: 0.6;
      font-size: 15px;
    }
    .top {
      display: flex;
      align-items: center;
      i {
        color: rgb(255, 209, 4);
        font-size: 25px;
        margin-right: 10px;
      }
      p {
        opacity: 1;
        font-size: 18px;
        margin: 0;
      }
    }
  }
}
